@font-face {
  font-family: font1;
  src: url(./Assets/static/Montserrat-Regular.ttf);
}
@font-face {
  font-family: font3;
  src: url(./Assets/static/FuturaCyrillicDemi.ttf);
}
@font-face {
  font-family: font2;
  src: url(./Assets/static/FuturaCyrillicBook.ttf);
}
@font-face {
  font-family: font4;
  src: url(./Assets/static/Montserrat-SemiBold.ttf);
}
@font-face {
  font-family: font5;
  src: url(./Assets/static/Montserrat-Bold.ttf);
}
@font-face {
  font-family: font6;
  src: url(./Assets/static/Hedvig_Letters_Sans/HedvigLettersSans-Regular.ttf);
}
.inter {
  font-family: font6;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: font2;
}
*:focus {
  outline: none;
}
@tailwind base;
@tailwind components;
@tailwind utilities;

.navbar {
  @apply flex justify-evenly lg:justify-between items-center gap-6 mx-3;
}
.nav-header {
  @apply flex justify-between items-center w-full md:w-auto mx-6 sm:mx-12 gap-6;
}
.nav-content {
  height: calc(100vh - 96px);
  @apply text-white w-full md:w-auto md:h-auto bg-zinc-950 absolute md:static top-[96px] lg:mx-12 duration-300;
  z-index: 1;
}
.navLinks {
  @apply flex flex-col md:flex-row items-center bg-zinc-950 justify-evenly my-12 md:my-6 gap-10 md:gap-10 lg:gap-12;
}
.nav-link {
  @apply text-xl md:text-lg hover:text-[#daa975] cursor-pointer duration-200;
}
.caraousel {
  @apply w-full h-[100vh] bg-zinc-800 duration-500 relative;
}
.caraousel-img {
  @apply w-full mix-blend-multiply h-full object-cover md:object-fill;
}
.caraousel-content {
  @apply absolute top-[45%] left-[15%] font-[font2];
}
.caraousel-controls {
  @apply text-4xl text-white absolute top-[50%] cursor-pointer;
}
.btn {
  @apply focus:outline-none  p-2 bg-[#F0F0F0] text-slate-700 text-lg hover:bg-[#A67A4B] hover:text-white hover:border-[#A67A4B] duration-300;
}
.btn-outline-white {
  @apply focus:outline-none py-3 px-7 my-10 hover:bg-white hover:text-black duration-300 text-white text-xl border-white border-[1px];
}
.btn-controls {
  @apply focus:outline-none  p-2 bg-[#8057d7] text-white text-lg duration-500;
}
.gender {
  @apply p-3 sm:p-5 w-full bg-[#e9e9e9] cursor-pointer border-[1px] rounded-xl flex flex-col items-center;
}
.gender-selected {
  @apply bg-[#8057D7] text-white border-[#8057D7];
}
.gender-selected > p {
  color: white;
}
.gender {
  transition: linear 0.2s;
}
.footer {
  @apply flex flex-col md:flex-row justify-around gap-8  items-start md:items-center p-10 my-6 font-[font2];
  align-self: self-end;
}
.accordion-icon {
  @apply text-xl cursor-pointer hover:text-slate-700;
}
.active {
  @apply bg-[#8057D7] text-white border-[#8057D7];
}
.input {
  @apply w-full p-2 bg-transparent border-0 border-b-[1px] border-b-black outline-none focus:outline-none hover:border-b-2 focus:border-b-2;
  transition: linear 0.1s;
}
.embla {
  overflow: hidden !important;
}
.embla__container {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 35%; /* Each slide covers 80% of the viewport */
}

@media (max-width: 768px) {
  .embla__container {
    grid-auto-columns: 100%;
  }
}
.discount {
  background: linear-gradient(
      to bottom,
      rgba(0, 47, 75, 0.5) 0%,
      rgba(220, 66, 37, 0.5) 100%
    ),
    url("./Assets/LivingRoom.jpg") no-repeat center/cover;
  height: 100vh;
  width: 100%;
}

#ruler,
.form-progress {
  transition: linear 0.4s;
}

@media (max-width: 281px) {
  #ruler {
    top: 34%;
  }
  #ruler1 {
    top: 0;
  }
  .btn-shop {
    font-size: 0.85rem !important;
    padding: 6px 15px !important;
  }
  .header > h1 {
    font-size: 1.2rem;
  }
  .header > p {
    font-size: 0.9rem;
  }
}
@keyframes slideDown {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.animation {
  animation: slideDown 0.3s ease-in 1;
}
@keyframes animate-width {
  from {
    width: 0%;
  }
  to {
    width: 75%;
  }
}
.animate-width {
  animation: animate-width 0.5s ease-in 1;
}
